.navbar {
  padding-bottom: 40px;
  outline: 1px solid red; }
  .navbar .ca-routes {
    width: 20%;
    display: flex;
    justify-content: space-between; }

.footer {
  outline: 1px solid red; }
  .footer .ca-routes {
    width: 30%;
    justify-content: space-between;
    display: flex; }

.ca-logo {
  margin-right: auto;
  width: 40px;
  height: 40px;
  border: 2px solid #071e3d;
  cursor: pointer;
  background: white;
  transition: 0.3s ease-in-out; }
  .ca-logo:hover {
    border: 2px solid #eaa81b;
    color: #eaa81b; }
  .ca-logo div {
    font-size: 20px; }

.link {
  padding-bottom: 8px;
  border-bottom: 2px solid transparent;
  -webkit-transition: all 200ms ease;
  transition: all 200ms ease;
  cursor: pointer; }
  .link:hover {
    border-bottom: 2px solid #071e3d; }

.link-inverse {
  padding-bottom: 8px;
  border-bottom: 2px solid #071e3d;
  -webkit-transition: all 200ms ease;
  transition: all 200ms ease;
  cursor: pointer;
  display: inline-table; }
  .link-inverse:hover {
    border-bottom: 2px solid transparent; }

.active-link {
  border-bottom: 2px solid #071e3d;
  cursor: pointer; }
  .active-link:hover {
    border-bottom: 2px solid transparent; }
